// app/preview/[category]/[id]/page.tsx
'use client'
import { notFound } from "next/navigation";
import ImageGallery from "./ImageGallery";
import { useGetPostDetail } from "@/app/lib/postServices/postQueries";

type Props = {
  params: {
    category: string; // car, phone, real-estate, ...etc
    id: string;
  };
};

export default function PostPreviewPage({ params }: Props) {
  const { category, id } = params;



  let isCar = false;
  let isApartment = false;
  let isHouse = false;
  let isMobile = false;
  let isElec = false;
  let isOutdoor = false;
  if (category === "cars") {
    isCar = true;
  } else if (category === "mobiles") {
    isMobile = true;
  } else if (category === "apartments") {
    isApartment = true;
  } else if (category === "houses") {
    isHouse = true;
  } else if (category === "electronics") {
    isElec = true;
  } else if (category === "outdoor-space") {
    isOutdoor = true;
  }

  const allImages = [
    { id: 0, image: data.cover_image }, // أضف cover_image كصورة أولى
    ...(data.gallery_images || []), // أضف gallery_images إذا وجدت
  ];

  return (
    <main className="grid md:grid-cols-2 gap-6 p-4">
      {/* القسم الأيسر: التفاصيل + السعر + تواصل */}
      <div className="space-y-4">
        <h1 className="text-2xl font-bold">{data.title}</h1>
        <p className="text-cgreen font-bold text-xl">{data.price} ليرة سورية</p>
        <p className="text-gray-600">{data.description}</p>

        {isMobile && (
          <div>
            <h2 className="text-lg font-semibold text-cgreen border-b pb-1 mb-2">
              تفاصيل المنتج
            </h2>
            <ul className="text-sm text-gray-700 space-y-1">
              {data.mobile?.brand && (
                <li>
                  <strong>العلامة التجارية: </strong>
                  {data.mobile.brand}
                </li>
              )}
              {data.mobile?.color && (
                <li>
                  <strong>اللون: </strong>
                  {data.mobile.color}
                </li>
              )}
              {data.mobile?.accessories !== undefined && (
                <li>
                  <strong>الإكسسوارات: </strong>
                  {data.mobile.accessories ? "نعم" : "لا"}
                </li>
              )}
            </ul>
          </div>
        )}

        <button className="w-full bg-cgreen text-white py-2 rounded hover:bg-chgreen transition">
          تواصل مع البائع
        </button>
      </div>

      {/* القسم الأيمن: المعرض */}
      <ImageGallery images={allImages} />
    </main>
  );
}